import React            from 'react';
import ReactDOM         from 'react-dom';
import IntlInjection    from './IntlInjection';
import { IntlProvider } from 'react-intl'

class ComponentWrapper {

  constructor(config) {
    this.init(config)
  }

  init(config) {

    const locale = (navigator.language) ? navigator.language : navigator.browserLanguage;

    ReactDOM.render(
      <IntlProvider locale={config.locale || locale || 'en'} key={locale}>
        <IntlInjection data={config} />
      </IntlProvider>,
      document.getElementById(config.elementId)
    )
  }

}


function init() {

    // Demo eventing API
    document.body.dispatchEvent(new CustomEvent('o.InitComponent', {
      detail: {
        elementId : 'demo-target1',
        greeting  : 'Hello world!'
      }
    }));

    // Demo direct API
    new ComponentWrapper({
      elementId : 'demo-target2',
      greeting  : 'Bonjour le monde!',
      locale    : 'fr'
    });

}



